// reset.scss import
@import './reset.scss';

// pink_btn
@mixin pink_btn(){
    background: $pink_color !important;
    color:#fff;
    box-shadow: 1px 1px 2px rgba(0,0,0,0.2);
    font-weight: bold;
}

// Detail
.detail{
    // detail 기본 셋팅
    color: $font_color;

    // title_box
    .title_box{
        width:50%;
        padding:20px;
    }

    // product_box
    .product_box{
        p{
            margin-bottom:0.5rem;
        }

        // 꽃 이미지
        .img_box{
            img{
                border-radius: 10px;
                box-shadow: 1px 1px 5px rgba(0,0,0,0.5);
            }
        }

        // 상품 소개 
        .text_box{
            // 주문하기, 뒤로가기 btn
            .btn{
                @include pink_btn();
            }
        }

        // my_alert
        .my_alert{
            margin-top:20px;
            width:50%;
            padding:15px 20px;
            background:$light_gray;
            border-radius: 10px;
            box-shadow: 1px 1px 2px rgba(0,0,0,0.2);
            
            p{
                margin:0;
                text-align: center;
                color:$pink_color;
            }
        }
    }
}

