// reset.scss import
@import './reset.scss';

// App
.App{
    color:$font_color;

    // jumbotron 
    .jumbotron{
      color:#fff;
      text-align: center;
      background:url('./img/main_bg.jpg') no-repeat center / cover;
      opacity:0.9;
      mix-blend-mode:darken;

      // jumotron 글자간 상하 간격
      p{
        margin:30px auto;
      }
    }

    // products 
    .products{
      // 상품 이미지
      img{
        width: 100%;
        height: 70%;
        object-fit: cover;
        border-radius: 10px;
        box-shadow: 3px 3px 10px rgba(0,0,0,0.3);
      }

      // 상품 설명
      .text_box{
        margin-top:10px;

        // 타이틀
        h3{
          color:$font_color;
        }
        // 출처 & 가격
        p{
          margin:0;
          color:$dark_gray;
        }
      }

      // more 버튼
      .more_btn{
        display: block;
        margin:10px auto 50px;
        box-shadow: 1px 1px 5px rgba(0,0,0,0.2);
      }
    }
}

// @extend .class명 --> 클래스 그대로 복붙하기

